/// Small Devices, Tablets
@media only screen and (max-width : 768px) {
	.conteiner {
		overflow: hidden; }
	body {
		padding-top: 3em; }
	.burger_menu {
		display: block; }
	.main_menu {
		display: none; }
	.head_body {
		.btn {
			font-size: 1em; }
		p {
			font-size: 3em; }
		.sticker {
			padding: 3em 0em 3em 2em;
			clip-path: polygon(85% 0, 100% 15%, 100% 100%, 0 100%, 0 0);
			ul {
				line-height: 1.5em; } } }

	.main_content {
		width: 100%;
		h1 {
			font-size: 2.5em; } }
	.side_bar {
		display: none; }
	.wrap_cards_doc {
		justify-content: center;
		.card_doc {
			&:after {
				display: none; } } }
	.wrap_services {
		flex-direction: column;
		justify-content: center;
		ul {
			flex-direction: column;
			height: auto;
			padding: unset; } }
	.main_services {
		width: 80%; }
	.over_services {
		width: 80%; }
	.adres {
		width: 80%;
		padding: 2em 0; }
	.wrap_soc_pay {
		flex-direction: column;
		.payment {
			width: 100%;
			div {
				padding-right: 0;
				justify-content: space-around; } } }
 }	///


